import React, {useRef} from "react" 
import "./index.css"
import ProfileBG from "../Home/Profile/pix.png"
  const Profile = () => {

   const Ref = useRef(null)
   const chooseImage = useRef(null)

   const selectImage = () => {
    chooseImage.current.click()
   }

   const pickImage = (e) => {
     const file = e.target.files[0]
     const reader =new  FileReader()
       reader.readAsDataURL(file)
       reader.onload =(event) => {
       Ref.current.src = event.target.result
       }
   }

    return(
        <>
           <div className="all_container">
             <div className="wrapper">
               <div className="header_container">
                 <div className="background_image_container">
                   <img className="img" ref={Ref} src={ProfileBG} alt="bd"/>
                   <div className="change_pix_container">
                   <div className="change_pix"  onClick = {()=> selectImage()} > 
                   change
                     <input type="file" ref={chooseImage}  onChange={pickImage}   />
                      </div>
                   </div>
                 </div>
               </div>
             </div>
           </div>
        </>
    )
  } 


  export default Profile